/**
 * JavaScript 
 * http://momentjs.cn/
 *
 * @dependency moment
 */
import moment from 'moment';

/**
 * JavaScript 
 * https://www.lodashjs.com/
 *
 * @dependency lodash
 */
const lodash = require('lodash');

/**
 * 
 */
export default class Util {
  static _ = lodash;

  /**
   *  {}  new Object() 
   *
   * @param value
   */
  static isObject(value: any) {
    return Object.prototype.toString.call(value) === '[object Object]';
  }

  /**
   * 
   *
   * @param value
   */
  static isArray(value: any) {
    // return Object.prototype.toString.call(value) === '[object Array]';
    return Array.isArray(value);
  }

  /**
   * 
   * NaN  number NaN
   *
   * @param value
   */
  static isNumber(value: any) {
    return typeof value === 'number' && !Number.isNaN(value);
  }

  /**
   * 
   *
   * @param value
   */
  static isFunction(value: any) {
    return typeof value === 'function';
  }

  /**
   * 
   *
   * @param value
   */
  static isNull(value: any) {
    if (value === null || value === undefined || value === '') {
      return true;
    } else if (this.isArray(value) && value.length === 0) {
      return true;
    } else if (this.isObject(value) && Object.keys(value).length === 0) {
      return true;
    }
    return false;
  }

  /**
   * 
   */

  /**
   * 
   * ()
   *
   * @param value Array
   * @param key String
   * @param order 'ASC': , 'DESC': , Function: 
   */
  static sort<T = any>(
    value: Array<T>,
    key: string,
    order: 'ASC' | 'DESC' | Function = 'ASC'
  ) {
    if (!this.isArray(value)) {
      return null;
    }
    value.sort((prev: T, next: T) => {
      let data;
      if (typeof order === 'function') {
        data = order(prev, next);
      } else {
        if (this.isObject(prev) && this.isObject(next)) {
          if (!this.isNull(key)) {
            // @ts-ignore
            const a = prev[key];
            // @ts-ignore
            const b = next[key];
            data = order === 'ASC' ? a < b : a > b;
          }
        } else if (!this.isArray(prev) && !this.isArray(next)) {
          data = order === 'ASC' ? prev < next : prev > next;
        }
      }
      return data ? -1 : 0;
    });
    return value;
  }

  /**
   * 
   *
   * @param date String | Date | Moment
   * @param format String http://momentjs.cn/docs/#/displaying/format/
   */
  static formatDate(
    date: string | Date | moment.Moment,
    format: string = 'YYYY-MM-DD'
  ) {
    if (this.isNull(date)) {
      return null;
    }
    return moment(date).format(format);
  }
}
